export function previewImage(img, index) {
	// 
	uni.previewImage({
		urls: img,
		current: index,
		longPressActions: {
			itemList: ['', '', ''],
			success: function(data) {
				console.log('' + (data.tapIndex + 1) + ',' + (data.index + 1) + '');
			},
			fail: function(err) {
				console.log(err.errMsg);
			}
		}
	});
}