import {
	getStorage,
	removeStorage,
	setStorage
} from '@/common/db.js';
import {
	constructNowUrl
} from '@/utils/urlUtils.js';
import {
	storageKey,
	baseURL
} from '@/common/config.js';
const {
	storage_token,
	storage_userInfo,
	storage_backRoute
} = storageKey

// 
// let requestQueue = new Map();
// 
function requestInterceptor(config) {
	//  headerstoken 
	//  token  headers 
	const token = getStorage(storage_token); //  token
	if (token) {
		config.header['token'] = `${token}`;
	}

	// 
	// requestQueue.set(config.url, config);

	return config;
}
// 
function paramsSerializer(params) {
	return Object.keys(params)
		.map((key) => encodeURIComponent(key) + '=' + encodeURIComponent(params[key]))
		.join('&');
}

// 
function statusCodeProcess(responseData) {
	const {
		data,
		code,
		msg
	} = responseData;

	// 
	switch (code) {
		case 1: // 
			// 
			return responseData;
		case 401: // 
			// 
			console.log('55555')
			// console.error(msg);
			removeStorage(storage_token)
			removeStorage(storage_userInfo)
			setTimeout(() => {
				uni.reLaunch({
					url: '/pages/login/login'
				})
			}, 300)

		default:
			uni.showToast({
				icon: 'none',
				title: msg
			})
			// 
			// console.error(msg);

	}
}


let that = this
// 
function responseInterceptor(response) {
	console.log('thisis', that)
	//  response.data response.statusCode 
	// uni.request 
	const {
		data,
		statusCode
	} = response;
	console.log('', response)
	// 
	switch (statusCode) {
		case 200: // 
			// 
			return statusCodeProcess(data);

		case 201: // 
			//  ID
			// return { ...data, newId: data.id }; // 
			return statusCodeProcess(data);

		case 400: // 
			// 
			console.error('Bad Request: ' + (data.message || data.msg || 'Invalid request parameters'));
			uni.showToast({
				icon: 'none',
				title: ''
			})
		case 401: // 
			// 
			// 
			return statusCodeProcess(data);

		case 403: // 
			// 
			console.error('Forbidden: You do not have access to this resource');
			uni.showToast({
				icon: 'none',
				title: ''
			})
		case 404: // 
			// 
			console.error('Not Found: The resource you requested could not be found');
			uni.showToast({
				icon: 'none',
				title: ''
			})
		case 500: // 
			// 
			uni.showToast({
				icon: 'none',
				title: ''
			})
			console.error('Internal Server Error:', data);

			// ...

		default:
			uni.showToast({
				icon: 'none',
				title: ''
			})
			// 
			console.error('Unknown Error:', statusCode, data);

	}
}

//  uni.request
export function httpRequest(url, method = 'GET', data = {}, header = {}) {
	//  GET  data  URL 
	if (method.toUpperCase() === 'GET') {
		// url += '?' + paramsSerializer(data);
		if (paramsSerializer(data)) {
			url += '?' + paramsSerializer(data);
		}
		console.log('', url, paramsSerializer(data));
	}

	// 
	const config = requestInterceptor({
		url,
		method,
		data,
		header
	});

	return new Promise((resolve, reject) => {
		uni.request({
			url: baseURL + 'api/' + config.url,
			method: config.method,
			data: config.method.toUpperCase() === 'GET' ? {} : config.data, // GET  data
			header: config.header,
			success: async (res) => {
				try {
					// 
					const responseData = responseInterceptor(res);
					//  responseInterceptor  Promise
					if (responseData instanceof Promise && (await responseData.catch())) {
						responseData.then(resolve).catch(reject);
					} else {
						resolve(responseData);
					}
				} catch (error) {
					// 
					reject(error);
				}
			},
			fail: (err) => {
				reject(err);
			}
		});
	});
}