//
export function debounceFun(func, delay=500) {
	//
	let timer;
	return function(...args) {
		// 
		clearTimeout(timer);
		timer = setTimeout(() => {
			func.apply(this, args);
		}, delay);
	};
}

//
export function throttleFun(func, delay=500) {
	//
	let timer = null;
	return function(...args) {
		if(!timer){
			timer = setTimeout(() => {
				//
				timer = null;
				console.log("")
				func.apply(this, args);
			}, delay);
		}
	};
}
